.content {
    z-index: 10;
    width: 100%;
    overflow: hidden;
    color: white;
    font-family: 'Poppins', sans-serif;
    display: flex;
    position: relative;

    &__side{
        position: absolute;
        width: 230px;
        top: 0;
        left: -230px;
        z-index: 5;
        height: 100%;
        background: linear-gradient(to right bottom, rgba(255, 255, 255, 0.7), rgba(255, 255, 255, 0.3));
        backdrop-filter: blur(0.1rem);
        transition: left 0.5s ease;
        box-shadow: 0px 0px 5px black;
        .arrow {
            height: 20px;
            position: absolute;
            bottom: 0%;
            left: 100%;
            color: crimson;
            display: flex;
            align-items: center;
            justify-content: center;
            font-size: 3rem;
            letter-spacing: -0.2rem;
            animation: arrowMove 1s ease infinite;
            cursor: pointer;
            animation: transform 1d ease forwards;
            width: 40px;
        }

        .rotate {
            transform: rotate(180deg);
            left: 105%;
        }
        @keyframes arrowMove {
            0%{
            letter-spacing: -0.7rem;
            }
            25%{
            letter-spacing: -0.9rem;
            }
            50%{
            letter-spacing: -1.1rem;
            }
            75%{
            letter-spacing: -1.3rem;
            }
            100%{
            letter-spacing: -1.5rem;
            }
        }

        .menu-container {
            .menu {
                color: white;
                list-style: none;
                margin: 0;
                text-transform: capitalize;
                padding: 0;
                .menu-item {
                    .submenu-btn {
                        padding-left: 4px;
                        display: block;
                        border-bottom: 1px solid black;
                        width: 100%;
                        text-decoration: none;
                        color: black;
                        background-color: white;
                        .submenu-arrow {
                            transition: all 0.5s;
                            margin: 3px;
                            float: right;
                            color: black;
                            width: 18px;
                            height: 18px;
                            background-image: url('../static/down-arrow.png');
                            background-size: cover;
                            background-position: center;
                            &:hover {
                                transform: scale(1.15);
                            }
                        }

                    }
                    .submenu {
                        height: 0;
                        overflow-y: hidden;
                        transition: all .5s;
                        .submenu-item {
                            cursor: pointer;
                            padding-left: 10px;
                            background-color: rgb(71, 70, 70);
                            margin: 2px 0;
                        }
                    }
                }
            }
        }
    }

    .show {
        left: 0;
    }
    &__tasks{
        h2 {
            text-shadow: 0 0 5px black;
        }
        .form-container {
            z-index: 10;
            position: fixed;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            background-color: rgba(0, 0, 0, 0.7);
            display: none;
            flex-direction: column;
            align-items: center;
            padding-top: 20px;
            .cancel {
                font-family: 'Poppins';
                position: absolute;
                top: 0;
                right: 0;
                margin: 25px 25px 0 0;
                display: flex;
                align-items: center;
                justify-content: center;
                width: 50px;
                height: 50px;
                font-weight: bolder;
                font-size: 2rem;
                cursor: pointer;
                border-radius: 50%;
                background-color: red;
                transition: font-size 0.1s ease;
                &:hover {
                    font-size: 1.5rem;
                }
            }
            form{
                border: solid black 1px;
                background-color: white;
                padding: 20px 25px;
                width: 50%;
                margin-top: 25px;
                color: black;
                border-radius: 10px;
                box-shadow: 0 0 5px white;
                input[type=submit], input[type=text], textarea, label {
                    display: block;
                }
                select {
                    width: 100%;
                    padding: 10px 12px;
                    margin: 5px 0 10px 0;
                }
                input[type=text], textarea {
                    outline: none;
                    width: 100%;
                    margin: 5px 0 10px 0;
                    padding: 10px 12px;
                    font-size: 1rem;
                    &:focus {
                        background-color: rgb(238, 238, 238);
                    }
                }
                input[type=submit] {
                    outline: none;
                    width: 100%;
                    margin: 5px 0 10px 0;
                    padding: 10px 12px;
                    font-size: 1rem;
                    cursor: pointer;
                }
                input[type=date] {
                    width: 135px;
                }

            }
        }
    }
}

body {
    display: flex;
    flex-direction: column;
    height: 100vh;
    background: linear-gradient(to right top, #65dfc9, #6cdbed);
    .header {
        height: 75px;
        width: 100%;
        background: linear-gradient(to right top, #f5db49, #f8863a);
    }
    .content {
        flex: 1;
    }
    .footer {
        background: linear-gradient(to right top, #f5db49, #f8863a);
        height: 75px;
    }
}